<template>
  <el-container class="review-el-container">
    <el-header class="review-el-header">
      <el-page-header @back="goBack" content="回顾复习"/>
    </el-header>
    <el-main class="review-el-main">

      <div style="height:60vh;">
        <table v-for="(error, index) in cerrorBookInfo.errorSet" :key="index">
          <tr>
            <td style="width:40vw;">{{error.question}}</td>
            <td style="width:5vw;" class="center">错误率<br/>{{error.frequency | getErrorRate(totalError)}}</td>
          </tr>
          <tr>
            <td>
              A、{{error.chose.A}}<br/>
              B、{{error.chose.B}}<br/>
              C、{{error.chose.C}}<br/>
              D、{{error.chose.D}}
            </td>
            <td class="center">
              <p>答案</p>
              <p>{{error.answer}}</p>
            </td>
          </tr><br/>
        </table>
      </div>
      
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: 'MainReview',
  data() {
    return {
      totalError: ''
    }
  },
  props: {
    cerrorBookInfo: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  filters: {
    getErrorRate(value, totalError) {
      return parseInt((value / totalError).toFixed(2) * 100) + '%'
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1)
    },
    getTotalError() {
      this.totalError = this.cerrorBookInfo.totalError
    }
  },
  created() {
    this.getTotalError()
  }
}
</script>

<style>
  .review-el-main {
    height: 72vh;
    margin: 0 32px;
  }
  .review-el-main table {
    border-collapse: collapse;
  }
  .review-el-main td {
    padding: 20px 32px;
    line-height: 2;
    border: 1px solid #000;
  }
  .center {
    text-align: center;
  }
  .isDisplay {
    display: unset
  }
</style>